<template>
  <div class="search-container">
    <!---->
    <!--Tips:  van-search  form  action  iOS -->
    <form action="/">
      <van-search
        v-model="value"
        show-action
        placeholder=""
        @search="onSearch"
        @cancel="onCancel"
        background="#496991"
        shape="round"
        @focus="isResultShow = false"
    />
    <!---->
    <search-result v-if="isResultShow" :value="value"></search-result>
    <!---->
    <search-suggestion v-else-if="value" :value="value" @search="onSearch"></search-suggestion>
    <!---->
    <search-history v-else :searchHistory.sync="searchHistory" @search="onSearch"></search-history>
</form>
  </div>
</template>

<script>
import SearchHistory from './components/search-history.vue'
import SearchSuggestion from './components/search-suggestion.vue'
import SearchResult from './components/search-result.vue'
import storage from '@/utils/storage'
export default {
  components: {
    SearchHistory,
    SearchSuggestion,
    SearchResult
  },
  data () {
    return {
      value: '',
      isResultShow: false, // /\
      searchHistory: storage.getItem('search-histories') || [] // 
    }
  },
  methods: {
    onSearch (value) { // 
      this.isResultShow = true // 
      this.value = value // 

      // 
      // : , 
      const index = this.searchHistory.indexOf(value)
      if (this.searchHistory.indexOf(value) !== -1) {
        this.searchHistory.splice(index, 1)
      }
      this.searchHistory.unshift(value)
    },
    onCancel () { // 
      console.log('')
      this.$router.back()
    }
  },
  watch: {
    searchHistory () {
      storage.setItem('search-histories', this.searchHistory)
    }
  }
}
</script>

<style lang="less" scoped>
.search-container {
  padding-top: 108px;
  .van-search__action{
    color:#fff;
  }
  .van-search {
    position: fixed;
    left:0;
    right:0;
    top:0;
    z-index:1;
  }
}
</style>
