<template>
  <div class="search-result">
    <!--list -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text=""
      @load="onLoad"
      :error.sync="error"
      error-text=""
    >
      <van-cell v-for="item in list" :key="item.id" :title="item.title" @click="$router.push(`article/${item.art_id}`)"/>
    </van-list>
  </div>
</template>
<script>
import { gerSearchResult } from '@/api/search'
export default {
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      page: 1, // 
      perPage: 10, // 
      error: false
    }
  },
  props: {
    value: {
      type: String,
      required: true
    }
  },
  methods: {
    async onLoad () {
      try {
        // 1. 
        const { data } = await gerSearchResult({
          page: this.page,
          per_page: this.perPage,
          q: this.value
        })
        // 2.  list
        const { results } = data
        this.list.push(...results)

        // 3.  loading
        this.loading = false
        // 4.  
        if (results.length) {
          // , 
          this.page++
        } else {
          //  finished 
          this.finished = true
        }
      } catch (err) {
        // 
        this.error = true
        this.loading = false
      }
    }
  }
}
</script>

