<template>
  <!---->
  <div class="search-suggestion">
    <van-cell v-for="(item, index) in suggestions" :key="index" icon="search" @click="$emit('search',item)">
      <template #title>
        <span v-html="highlight(item)"></span>
      </template>
    </van-cell>
  </div>
</template>

<script>
import { getSuggestions } from '@/api/search'
import { debounce } from 'lodash'
export default {
  props: {
    value: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      suggestions: [] // 
    }
  },
  watch: { // watch 
    value: {
      handler: debounce(function (newValue) {
        this.getSuggestions(newValue)
      }, 500),
      immediate: true
    }
  },
  methods: {
    async getSuggestions (q) {
      try {
        const { data } = await getSuggestions(q)
        this.suggestions = data.options
      } catch (err) {
        this.$toast(', ')
      }
    },
    highlight (text) {
      const highlightString = `<span style="color:red">${this.value}</span>`

      //  ,  new RegExp()
      // 1 : , 
      const reg = new RegExp(this.value, 'gi')
      return text && text.replace(reg, highlightString)
    }
  }
}
</script>

